import { Injectable } from "@angular/core";

@Injectable()
export class MoneyUtilService {
    /**
     * @description: 
     * @param num {string | number} 
     * @param cent {string | number} 
     * @param isThousand 
     * @return: 
     */
    formatThousandth(num, cent, isThousand: boolean): string {
        const reg=/\d{1,3}(?=(\d{3})+$)/g;
        let result;
        if (num || num === 0) {
            num = num.toString().replace(/$|,/g, '');
            const dotIndex = num.indexOf('.');
            if (dotIndex > -1) {
                let dotLeft = num.substring(0, dotIndex);
                if (isThousand) {
                    dotLeft = (dotLeft + '').replace(reg, '$&,');
                }
                let dotRight = num.substring(dotIndex + 1, (dotIndex + cent + 1));
                if (dotRight.length < cent) {
                    while (dotRight.length < cent) {
                        dotRight += '0';
                    }
                }
                result = `${dotLeft}.${dotRight}`;
            } else {
                if (isThousand) {
                    result = (num + '').replace(reg, '$&,');
                } else {
                    result = num;
                }
                if (cent > 0) {
                    result += '.';
                    for (let i = 0; i < cent; i++) {
                        result += '0';
                    }
                }
            }
        }
        
        return result;
        // if (isNaN(cent)) {
        //     cent = 0;
        // }
        // cent = parseInt(cent);
        // cent = Math.abs(cent);
        // var sign = (num == (num = Math.abs(num)));
        // num = Math.floor(num * Math.pow(10, cent) + 0.50000000001); //..
        // var cents: any = num % Math.pow(10, cent); //.
        // num = Math.floor(num / Math.pow(10, cent)).toString();//.
        // cents = cents.toString();//,.
        // while (cents.length < cent) {//.
        //     cents = "0" + cents;
        // }
        // if (!isThousand) { //.
        //     return (((sign) ? '' : '-') + num + '.' + cents);
        // }
        // //.
        // for (var i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++) {
        //     num = num.substring(0, num.length - (4 * i + 3)) + ',' + num.substring(num.length - (4 * i + 3));
        // }
        // return (((sign) ? '' : '-') + num + '.' + cents);
        // const reg=/\d{1,3}(?=(\d{3})+$)/g;
        // return (num + '').replace(reg, '$&,');
    }

    toNumber(money: any) {
        if (money) {
            money = typeof money === 'number' ? money.toString() : money;
            return new Number(money.replace(/$|,/g, ''));
        }
        return money;
    }
}
